@import './reset.scss';

$메인칼라 : #ff0000;

.red {
  color: $메인칼라;
}

div.container h4 {
  color: green;
}

div.container { // 셀렉터 대신 쓰는 nesting
  h4 {
    color: blue;
  }

  p {
    color: green;
  }
}

@mixin 함수() {
  background-color: #eee;
  padding: 20px;
  border-radius: 5px;
  max-width: 500px;
  width: 100%;
  margin: auto;
}

.my-alert {
  @include 함수()
  p {
    margin-bottom: 0;
  }
}

.my-alert2 {
  @extend .my-alert;
  background-color: #ffe591;
}
